@import url(https://fonts.googleapis.com/css2?family=Lato&family=Poppins:wght@400&family=Roboto+Mono:wght@400&family=Source+Code+Pro&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*,
html {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: "Poppins", sans-serif;
  background: #f9f9f9;
  box-sizing: border-box;
}

.App {
  text-align: center;
}

.form {
  width: 100%;
  background: linear-gradient(to right, #bc4e9c, #f80759);
  padding: 10px;
  border-bottom-left-radius: 35px;
  border-bottom-right-radius: 35px;
  box-shadow: 5px 0 20px rgba(0, 0, 0, 0.6);
  text-align: center;
}

.form h1 {
  font-size: 2.9rem;
  color: #fff;
  letter-spacing: 8px;
  text-transform: uppercase;
  font-weight: bold;
  margin-bottom: 6px;
}

input {
  padding: 10px 15px;
  width: 280px;
  border-radius: 20px;
  border: none;
  outline: none;
  margin: 7px 19px 18px 12px;
  font-size: 0.9rem;
  font-family: "Roboto Mono", monospace;
  letter-spacing: 1px;
  box-shadow: 0px 5px 10px rgba(0, 0, 0, 0.5);
}

.search-btn {
  padding: 10px 25px;
  border-radius: 20px;
  border: none;
  outline: none;
  box-shadow: 0px 5px 10px rgba(0, 0, 0, 0.5);
  cursor: pointer;
  background: #fff;
  text-transform: uppercase;
  letter-spacing: 1px;
  margin-bottom: 18px;
  font-weight: bold;
  font-family: "Poppins", sans-serif;
  transition: 0.4s linear;
}

.search-btn:hover {
  background: #000;
  color: #fff;
  font-weight: bold;
}

@media only screen and (max-width: 768px) {
  .form h1 {
    font-size: 2.5rem;
    margin: 0;
  }

  input {
    padding: 8px 12px;
    width: 200px;
  }

  .search-btn {
    padding: 8px 18px;
    border-radius: 20px;
  }

  .full-btn {
    padding: 5px 8px;
  }

  .active-recipe-img {
    width: 100%;
  }

  .active-recipe-ingredients .ingredient-list {
    width: 100%;
    margin: 5px auto;
  }

  .active-recipe-footer .back {
    padding: 5px 15px;
  }
  .active-recipe-publisher {
    font-size: 1rem;
  }
}

.recipes {
  border: 1px solid #b1aeae;
  margin: 10px;
  margin-top: 35px;
  border-radius: 30px;
  display: flex;
  justify-content: space-around;
  flex-flow: wrap;
  min-height: 68vh;
}

.recipes .card {
  background: #fff;
  box-shadow: 0px 4px 20px rgba(0, 0, 0, 0.3);
  margin: 20px;
  text-align: center;
  border-radius: 20px;
}

.recipes .card img {
  border-radius: 20px;
  border: 4px solid #fff;
  height: 250px;
  width: 100%;
}

.recipes .card h5 {
  padding: 5px 8px;
  font-size: 1.4rem;
  letter-spacing: 1.3px;
}

.recipes .card p {
  letter-spacing: 1px;
  padding: 0 8px;
}

.btn-section {
  display: flex;
  justify-content: space-evenly;
}

.btn {
  margin: 25px 7px 17px 7px;
  font-size: 0.8rem;
  border-radius: 20px;
  outline: none;
  cursor: pointer;
  border: none;
  text-transform: uppercase;
  text-align: center;
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.3);
}

.full-btn {
  background: #000;
  color: #fff;
  text-align: center;
  padding: 9px 16px;
  letter-spacing: 0.6px;
  position: relative;
  z-index: 1;
  border: 2px solid black;
}
.full-btn::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: #ffffff;
  z-index: -1;
  border-radius: 20px;
  transform: scaleX(0);
  transform-origin: center;
  transition: transform 0.4s ease-in-out;
}

.full-btn:hover::before {
  transform-origin: center;
  transform: scaleX(1);
}

.full-btn:hover span {
  color: #000;
  font-weight: bold;
}

.ingre-btn {
  border: 2px solid #262626;
  position: relative;
  z-index: 1;
  overflow: hidden;
  letter-spacing: 0.6px;
  background: transparent;
  padding: 9px 16px;
}

.ingre-btn::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: #000;
  z-index: -1;
  border-radius: 20px;
  transform: scaleX(0);
  transform-origin: center;
  transition: transform 0.4s ease-in-out;
}

.ingre-btn:hover::before {
  transform-origin: center;
  transform: scaleX(1);
}

.ingre-btn:hover span {
  color: #fff;
  font-weight: bold;
}

.active-recipe {
  margin: 10px;
  padding: 10px;
  /* border: 1px solid #000; */
}

.active-recipe-img {
  box-shadow: 03px 5px 15px rgba(0, 0, 0, 0.6);
  display: block;
  width: 100%;
  max-width: 400px;
  margin: auto;
  height: auto;
}

.active-recipe-title {
  font-size: 1.8rem;
  margin-top: 20px;
  text-transform: uppercase;
  letter-spacing: 3px;
}

.ingredients-heading {
  margin: 25px;
  text-transform: uppercase;
  letter-spacing: 2px;
  font-size: 1.1rem;
}

.active-recipe-ingredients {
  width: 100%;
  padding: 0 10px;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(240px, 1fr));
  grid-gap: 1.5em;
}

.active-recipe-ingredients .ingredient-list {
  list-style-type: none;
  padding: 10px 15px;
  letter-spacing: 0.6px;
  text-align: center;
  box-shadow: 1px 3px 10px rgba(0, 0, 0, 0.3);
  background: #fff;
  cursor: pointer;
  position: relative;
}

.active-recipe-ingredients .ingredient-list::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  background: #bc4e9c;
  width: 100%;
  height: 5px;
  transform: scaleX(0);
  transform-origin: center;
  transition: transform 0.4s ease-in-out;
}

.active-recipe-ingredients .ingredient-list:hover::after {
  /* width: 100%; */
  transform: scaleX(1);
  transform-origin: center;
}

.active-recipe-ingredients .ingredient-list p {
  font-size: 0.9rem;
}

.active-recipe-ingredients .ingredient-list p span {
  font-weight: bold;
}

.active-recipe-footer {
  display: flex;
  justify-content: space-evenly;
  margin-top: 50px;
}

.active-recipe-footer .back {
  padding: 10px 25px;
  border-radius: 20px;
  border: 2px solid #000;
  background: transparent;
  color: #000;
  font-weight: bold;
  text-align: center;
  letter-spacing: 0.6px;
  outline: none;
  cursor: pointer;
  position: relative;
  z-index: 1;
}

.active-recipe-footer .back::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: #000;
  border-radius: 20px;
  z-index: -1;
  border-radius: 20px;
  transform: scaleX(0);
  transform-origin: center;
  transition: transform 0.4s ease-in-out;
}

.active-recipe-footer .back:hover::before {
  transform-origin: center;
  transform: scaleX(1);
}

.active-recipe-footer .back:hover span {
  color: #fff;
  font-weight: bold;
}

